@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600');
@import '~highlight.js/styles/atom-one-light.css';

html, body, #root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  color: rgba(0,0,0,0.8);
  overflow: hidden;
}

a, a:focus, a:active {
  color: #4b87b0;
  text-decoration: none;
}

ul li {
  margin: 10px 0;
}

ul li a {
  font-size: 14px;
}

code {
  background-color: #f9f2f4;
  border-radius: 4px;
  color: #ff00ba;
  display: inline-block;
  padding: 2px 4px;
}

.display-2 {
  margin: 0;
  font-weight: 400;
}

.body {
  font-size: 14px;
  line-height: 20px;
}

.secondary {
  color: rgba(0,0,0,0.4);
}

.font-weight-semi-bold {
  font-weight: 600;
}

.app {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.main-menu {
  display: flex;
  flex-direction: column;
  background-color: #333333;
  color: #FFFFFF;
  height: 100%;
  width: 280px;
  overflow-y: auto;
}

.main-menu img {
  height: 60px;
  width: 60px;
  margin-left: 20px;
}

.brand-name {
  margin-left: 20px;
  color: #FFFFFF;
}
.brand-name:focus, .brand-name:active {
  color: #FFFFFF;
}

.brand-name h1 {
  font-weight: 300;
}

.tab button {
  font-size: 14px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 12px 24px;
  cursor: pointer;
  background-color: transparent;
  outline: none;
  border: none;
  text-decoration: none;
  font-weight: 600;
}

.tab.active {
  border-left: 4px solid #99ccee;
  padding-left: 20px;
  color: #FFFFFF;
}

.tab.sub-tab {
  padding-left: 20px;
}

.tab.active.sub-tab {
  padding-left: 40px;
}

.tab.active button {
  padding-left: 0;
}

.app-content {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  overflow-y: auto;
}

.page {
  padding: 40px;
  width: 100%;
  max-width: 1024px;
  position: relative;
}

.page-switch {
  float: right;
  clear: both;
}

